@font-face {
    font-family: "iconfont";
    src: url('../fonts/iconfont.eot');
    src: url('../fonts/iconfont.eot?#iefix') format('eot'),
         url('../fonts/iconfont.woff') format('woff'),
         url('../fonts/iconfont.woff2') format('woff2'),
         url('../fonts/iconfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

$icon-arrow-left-fill: "\EA01";
$icon-arrow-left: "\EA02";
$icon-arrow-right-fill: "\EA03";
$icon-arrow-right: "\EA04";


%icon {
    font-family: "iconfont";
    font-style: normal;
    font-weight: normal;
    text-rendering: auto;
    speak: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon {
    @extend %icon;
    display: inline-block;
}

.icon-arrow-left-fill:before { content: $icon-arrow-left-fill; }
.icon-arrow-left:before { content: $icon-arrow-left; }
.icon-arrow-right-fill:before { content: $icon-arrow-right-fill; }
.icon-arrow-right:before { content: $icon-arrow-right; }
